<template>
	<view class="container">
		<view class="banner-box" :style="{'background-color':topBackgroundColor,'padding-top':navHeight+'px'}">
			<!-- #ifdef MP-WEIXIN -->
			<view class="search-box" :style="{'height':navHeight+2+'px','background-color':topBackgroundColor}">
				<navigator url="/pages/goods/search" class="input-box" :style="{'height':customHeight+'px'}">
					<image src="../../static/icon/search.png" class="icon">
					<view class="text">查找商品 如:苹果</view>
				</navigator>
			</view>
			 <!-- #endif -->
			<uni-swiper-dot :info="banner" :current="current" field="content" mode="dot">
				<swiper class="banner-swiper" @change="swiperChange">
					<swiper-item v-for="(item ,index) in banner" :key="item.id">
						<view class="swiper-item">
							<image :src="item.image" class="img"></image>
						</view>
					</swiper-item>
				</swiper>
			</uni-swiper-dot>
		</view>
		<view class="haibao-box">
			<image src="../../static/images/haibao.jpg" class="img" mode="aspectFill"></image>
		</view>
		<view class="group-box">
			<uni-grid :column="4" :highlight="true"  :showBorder="false">
				<uni-grid-item v-for="(item, index) in group"  :key="item.id">
					<view class="item" @click="toGoodsGroup(item.id)" >
						<image :src="item.image" class="img"></image>
						<text class="text">{{item.name}}</text>
					</view>
				</uni-grid-item>
			</uni-grid>
		</view>
		<view class="goods-box">
             <view class="title-box">
				 <uni-icons type="fire-filled" size="30" color="#ff557f"></uni-icons>
				 <text class="title">推荐商品</text>
			 </view>
			 <view class="goods-list">
				 <view class="goods-item" v-for="(item,index) in goods" :key="item.id" @click="toDetail(item.id)">
				 	<view class="l">
				 		<image :src="item.image" class="img"></image>
				 		<view class="info">
				 			<view class="name line">{{item.title}}</view>
				 			<view class="dec line2">{{item.describe}}</view>
				 			<view class="price">￥{{item.price}}/份</view>
				 		</view>
				 	</view>
				 	<view class="r" @click.stop="addCartGoods(item.id)">
				 		<image src="../../static/icon/gouwuche.png" class="icon"></image>
				 	</view>
				 </view>
			 </view>
		</view>
	</view>
</template>

<script>
	import {mapMutations,mapActions} from 'vuex';
	const app = getApp()
	export default {
		data() {
			return { 
				customHeight:app.globalData.customHeight, //胶囊高度
				navHeight:app.globalData.navHeight,//状态栏高度 + 导航栏高度
				isfocus:false,
				bs: [
					"#00828B",
					"rgb(255,85,127)",
					"rgb(255,170,0)",
					"rgb(170,0,0)",
				],
				topBackgroundColor: '#00828B',
				current: 0,
				banner: [],
				group: [],
				goods: []
			}
		},
		onLoad: function() {
		},
		onShow:function(){
			this.getData()
		},
		methods: {
			...mapMutations(['setGroupId','setGoodsGroup']),
			...mapActions(['addCartGoods']),
			swiperChange(e) {
				this.current = e.detail.current;
				let index = this.current % 4
				this.topBackgroundColor = this.bs[index]
			},
			// 获取首页数据
			async getData() {
				const response = await this.$api.goods.getIndexData()
				if (response.data.code === 200) {
					let data = response.data.data
					this.banner = data.banner
					this.group = data.group
					this.goods = data.goods
					this.setGoodsGroup(data.group)
				}
			},
			// 跳转商品分类页面
			toGoodsGroup(groupId){
				// 将选中的分类id保存到vuex，因为switchTab跳转不能带参
				this.setGroupId(groupId)
				uni.switchTab({
					url: '/pages/goods/group'
				});
			},
			// 跳转商品详情
			toDetail(id){
				uni.navigateTo({
					url:'/pages/goods/detail?goodsId='+id
				})
			}
		},
		// 点击搜索输入框跳转搜索页
		onNavigationBarSearchInputClicked(){
			uni.navigateTo({
				url:'/pages/goods/search'
			})
		}
	}
</script>

<style lang="scss">
	.container {
		.banner-box {
			width: 100%;
			padding: 0 20upx 20upx;
			/* #ifdef MP-WEIXIN */
			.search-box{
				background-color: #00828B;
				position: fixed;
				width: 100%;
				top: 0;
				left: 0;
				z-index:100;
				display: flex;
				align-items: flex-end;
				justify-content: center;
				padding:0 20upx 12upx 20upx;
				.input-box{
					width: 100%;
					background-color: rgba(#fff, .4);
					border-radius: 50upx;
					display: flex;
					align-items: center;
					justify-content: center;
					.icon{
						width: 45upx;
						height: 45upx;
						margin-right: 10upx;
					}
					.text{
						color: #5c6366;
						font-size: 28upx;
					}
				}
			}
			 /* #endif */
			.banner-swiper {
				width: 100%;
				height: 300upx;
				border-radius: 10upx;
				overflow: hidden;
				.swiper-item {
					.img {
						width: 100%;
						height: 300upx;
					}
				}
			}
		}
		.haibao-box {
			background-color: $uni-white;
			width: 100%;
			height: 220upx;
			padding: 20upx;
			.img {
				width: 100%;
				height: 180upx;
				border-radius: 100upx;
			}
		}
		.group-box {
			background-color: $uni-white;
			padding: 12upx 0;
			margin-top: 10upx;
			.uni-grid-item{
				align-items: center;
				.item {
					display: flex;
					flex-direction: column;
					align-items: center;
					.img {
						width: 120upx;
						height: 120upx;
						border-radius: 50%;
					}
					.text {
						margin-top: 8upx;
					}
				}
			}
		}
		.goods-box{
			.title-box{
				display: flex;
				align-items: center;
				height: 70upx;
				padding: 0 20upx;
				.title{
					font-size: 30upx;
					font-weight: bold;
					color: #ff557f;
					margin-left: 10upx;
				}
			}
			.goods-list{
				padding: 0 20upx;
				.goods-item{
					display: flex;
					align-items: center;
					justify-content: space-between;
					background-color: #fff;
					border-radius: 8upx;
					padding: 40upx;
					margin-bottom: 10upx;
					.l{
						display: flex;
					    justify-content: flex-start;
						.img{
							width: 200upx;
							height: 200upx;
							border-radius: 12upx;
						}
						.info{
							margin-left: 20upx;
							width: 320upx;
							.name{
								font-size:30upx;
								font-weight: bold;
								color: $uni-main-color;
								height: 50upx;
								line-height: 50upx;
							}
							.dec{
								font-size: 22upx;
								height: 72upx;
								line-height: 36upx;
								color: $uni-secondary-color;
								margin: 15upx 0 25upx;
							}
							.price{
								font-size: 26upx;
								font-weight: bold;
								color: #ff557f;
								font-family: '雅黑';
							}
						}
					}
					.r{
						.icon{
							width: 50upx;
							height: 50upx;
						}
					}
				}
			}
		}
	}
</style>